.LoginScreen{
    position: absolute;
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    background: url(/playtimee/static/media/ParkBasketball.4f097d08.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.LoginScreen h1, .LoginScreen h2 {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 5px;

}

.LoginScreen form{
    display: flex;
    flex-direction: column;
}
.LoginScreen input{
    margin: 5px;
}

.LoginScreen input{
    opacity: 60%;
    border: 0;
    height: 20px;
    border-radius: 2px;
    transition-duration: 0.5s;
}

input:hover{
    opacity: 100%;
    transition-duration: 0.5s;
}

.loginbutton{
    border: none;
    border-radius: 2px;
    padding: 5px;
}

.loginbutton:hover {
    transition-duration: .5s;
    background-color: rgb(235, 188, 118);
    color: gray;
}
