.HomePage{
    display: flex;
    width: 100vw;
    height: 100vh;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
}
    .navigationsidebar{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        min-width: 18%;
        height: 100%;
        box-sizing: border-box;
        padding-bottom: 10%;
        list-style: none;
        background: rgb(23,92,103);
        background: linear-gradient(0deg, rgba(23,92,103,1) 0%, rgba(20,62,75,1) 4%, rgba(16,29,45,1) 17%);
